import React from 'react';
import PropTypes from 'prop-types';

import { withStyles } from '../tools/withStyles';
import { Link } from '../components/Link';
import { Main } from '../components/Main';
import { Text } from '../components/Text';
import { Fader } from '../components/Fader';
import { Secuence } from '../components/Secuence';

const styles = theme => ({
  root: {}
});

class About extends React.Component {
  static propTypes = {
    classes: PropTypes.object
  };

  render () {
    const { classes } = this.props;

    return (
      <Main className={classes.root}>
        <article>
          <Secuence stagger>
            <header>
              <h1 style={{textAlign:"center"}}><Text>About Swifty</Text></h1>
            </header>
            <b><p style={{textAlign:"center"}}><Text> Why hit the gym to be fit? When all you need is Swifty - Your personal trainer </Text></p></b>
            <p><Text>"Being fit was never simpler"</Text></p>
            <Fader style={{ display: 'block' }}>
                <img
                  src='/images/swifty1.png'
                  style={{ width: '100%', maxWidth: '400px' }}
                />
              </Fader>
            <p><Text>Swifty is your AI trainer who helps you stay in shape without the need of traveling to gyms,yoga centers or any other location,we bring the convenience to your home.</Text></p>
            <p><Text>"Everyone loves games"-Fitness is a domain that is driven by motivation and we believe that gamification will make you go the extra mile. </Text></p>
            <Fader style={{ display: 'block' }}>
                <img
                  src='/images/swifty2.png'
                  style={{ width: '100%', maxWidth: '400px' }}
                />
              </Fader>
            <p><Text>We at swifty believe that fitness shouldnt be bound by time and space,it should always be available whenever and wherever one chooses.Compete with yourself and push your limits,track your progress daily </Text></p>
            <Fader style={{ display: 'block' }}>
                <img
                  src='/images/swifty3.png'
                  style={{ width: '100%', maxWidth: '400px' }}
                />
              </Fader>
              <p><Text>Swifty is available for beta testing on android and will soon be available on iOS. Please feel free to reach us at support@swifty,fit </Text></p> 
            <Fader style={{ display: 'block' }}>
            <a href='https://play.google.com/store/apps/details?id=fit.swifty.swifty'>
                <img
                  src='/images/comingsoon.png'
                  style={{ width: '100%', maxWidth: '400px' }}
                />
              </a>
              </Fader>
          </Secuence>
        </article>
      </Main>
    );
  }
}

export default withStyles(styles)(About);
